body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  position: absolute;
  left: 0;
  right: 0;
}

.section-catalog {
  background-color: #111;
}

.search-form {
  justify-content: center;
  gap: 16px;
  display: flex;
  position: relative;
}

.search-input {
  color: #b7b7b7;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
}

.search-button {
  color: #fff;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-button:hover, .search-button:focus {
  background: #fff;
  border: 1px solid #f87719;
  transition: all 1s;
}

.search-icon {
  width: 18px;
  height: 18px;
  fill: rgba(0, 0, 0, 0);
  stroke: #fff;
}

.search-button:hover .search-icon, .search-button:focus .search-icon {
  stroke: #f87719;
  transition: all 1s;
}

.cancel-button:hover .cancel-icon, .cancel-button:focus .cancel-icon {
  stroke: #282828;
  transition: all 1s;
}

.cancel-icon {
  width: 18px;
  height: 18px;
  stroke: #fff;
}

.error-container {
  width: 280px;
  text-align: center;
  margin-top: 48px;
  margin-left: auto;
  margin-right: auto;
}

.error-information {
  text-align: center;
  color: #fff;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.4;
}

.error-container div {
  flex-direction: column;
  gap: 0;
  display: flex;
}

.arrow-button {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
}

.arrow-left-icon {
  width: 20px;
  height: 20px;
}

.arrow-right-icon {
  width: 20px;
  height: 20px;
  rotate: 180deg;
}

.pagination-pages-list {
  gap: 8px;
  display: flex;
}

.card-item {
  width: 280px;
  height: 406px;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.6;
  position: relative;
  overflow: hidden;
}

.film-poster {
  width: 100%;
  height: 100%;
}

.film-title {
  text-transform: uppercase;
  color: #fff;
}

.film-description, .film-rating {
  color: #b7b7b7;
}

.overlay {
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  align-items: flex-end;
  padding: 12px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.film-info {
  width: 100%;
  z-index: 1;
  position: relative;
}

.film-details {
  justify-content: space-between;
  display: flex;
}

.pagination-dots {
  width: 8px;
  height: 10px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  font-size: 10px;
  font-weight: 400;
}

@media screen and (min-width: 320px) {
  .section-catalog {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .search-form {
    margin-bottom: 16px;
  }

  .search-input {
    width: 224px;
    padding: 12px 20px;
  }

  .cancel-button {
    width: 18px;
    height: 18px;
    color: #fff;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    position: absolute;
    top: 13px;
    right: 77px;
  }

  .search-button {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }

  .movie-list {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    margin: 0 0 20px;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .pagination-wrapper {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
  }

  .pagination-item {
    align-items: center;
    display: flex;
  }

  .pagination-button {
    width: 24px;
    height: 24px;
    color: #b7b7b7;
    cursor: pointer;
    background: #111;
    border: 1px solid;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    font-weight: 400;
    line-height: 1;
    display: flex;
  }

  .pagination-button.is-active {
    background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
    border: none;
  }

  .pagination-button:hover, .pagination-button:focus {
    background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
    border: none;
    transition: all 1s;
  }
}

@media screen and (min-width: 768px) {
  .section-catalog {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .search-form {
    margin-bottom: 36px;
  }

  .search-input {
    width: 204px;
    padding: 14px 20px;
  }

  .cancel-button {
    top: 15px;
    right: 300px;
  }

  .search-button {
    width: 44px;
    height: 44px;
  }

  .movie-list {
    flex-flow: wrap;
    gap: 32px 16px;
    margin-bottom: 44px;
    display: flex;
  }

  .item {
    width: 224px;
    height: 325px;
    margin-bottom: 0;
  }

  .pagination-wrapper {
    gap: 12px;
  }

  .pagination-button {
    width: 40px;
    height: 40px;
    font-size: 14px;
  }

  .pagination-dots {
    width: 12px;
    height: 14px;
    font-size: 14px;
    font-weight: 500;
  }

  .arrow-button {
    width: 28px;
    height: 28px;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
  }

  .arrow-left-icon, .arrow-right-icon {
    width: 28px;
    height: 28px;
  }

  .pagination-pages-list {
    gap: 16px;
    display: flex;
  }

  .error-container {
    width: 513px;
    margin-top: 80px;
  }

  .error-information {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.4;
  }

  .error-container div {
    gap: 5px;
  }

  .card-item {
    width: 224px;
    height: 325px;
  }
}

@media screen and (min-width: 1280px) {
  .section-catalog {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .search-form {
    margin-bottom: 48px;
  }

  .search-input {
    width: 334px;
    padding: 14px 24px;
    font-size: 16px;
    line-height: 1.18;
  }

  .search-button {
    width: 48px;
    height: 48px;
  }

  .search-icon {
    width: 20px;
    height: 20px;
  }

  .cancel-button {
    top: 15px;
    right: 500px;
  }

  .movie-list {
    margin-bottom: 60px;
  }

  .item {
    width: 395px;
    height: 574px;
  }

  .card-item {
    width: 395px;
    height: 574px;
    font-size: 20px;
    line-height: 1.15;
  }

  .film-title {
    margin-bottom: 8px;
  }

  .overlay {
    padding: 20px;
  }

  .error-container {
    width: 684px;
  }

  .error-container div {
    gap: 10px;
  }

  .error-information {
    font-size: 32px;
    line-height: 1.12;
  }
}

